@import 'variables';

header {
  padding-top: 20px;
  position: fixed;
  top: 0;
  height: 60px;
  width: 100%;
  border-bottom: solid 5px rgb(255, 255, 255);
  background-color: $primary-blue;
  color: rgb(255, 255, 255);
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 1;
}
