ite alternate ease-in-out;animation:arrow 1s infinite alternate ease-in-out}@-webkit-keyframes arrow{0%{-webkit-transform:translateX(-0.1em);transform:translateX(-0.1em);opacity:0.4}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:0.9}}@keyframes arrow{0%{-webkit-transform:translateX(-0.1em);transform:translateX(-0.1em);opacity:0.4}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:0.9}}.card{background-color:#fff;box-shadow:0px 12px 0px 0px rgba(255,96,33,0.993);border-radius:10px;width:50%;margin:50px auto;padding:20px;text-align:center}.education-form{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:10px;gap:10px}.education-form p{border-bottom:2px solid black;margin:0 20px}.education-cont{padding-bottom:20px;margin-bottom:20px;border-bottom:3px solid rgba(255,96,33,0.993)}.school-name{grid-column:1/2}.school-name-input{grid-column:1/2}.study-title{grid-column:2/3;grid-row:1/2}.study-title-input{grid-column:2/3}h3{margin-top:10px;grid-column:1/3}.experience-form{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:10px;gap:10px}.experience-form p{border-bottom:2px solid black;margin:0 20px}.experience-cont{padding-bottom:20px;margin-bottom:20px;border-bottom:3px solid rgba(255,96,33,0.993)}.main-tasks{grid-column:1/3}.main-tasks-input{min-height:100px;grid-column:1/3}.company-name{grid-column:1/2}.company-name-input{grid-column:1/2}.position-title{grid-column:2/3;grid-row:1/2}.position-title-input{grid-column:2/3}header{padding-top:20px;position:fixed;top:0;height:60px;width:100%;border-bottom:solid 5px #fff;background-color:#00a4ce;color:#fff;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;z-index:1}.details-container{display:grid;grid-template-columns:repeat(2, 1fr);margin:10px 100px;grid-gap:0px;gap:0px}.details-container p{border-bottom:2px solid black;margin:0 20px}.details-container h2{marg